import React, { useState } from 'react';

const BlogForm = (props) => {
  const { addBlog, visible, toggleVisible } = props;
  const [title, setTitle] = useState('');
  const [author, setAuthor] = useState('');
  const [url, setUrl] = useState('');

  const handleSetTitle = event => setTitle(event.target.value)

  const handleSetAuthor = event => setAuthor(event.target.value)

  const handleSetUrl = event => setUrl(event.target.value)

  const handleAddBlog = () => {
    addBlog({ title, author, url })
  }

  const handleToggleVisible = () => {
    setTitle('')
    setAuthor('')
    setUrl('')
    toggleVisible()
  }

  return (
    <div>
      {visible &&
        <div>
          <div>
            title:
          <input
              type="text"
              value={title}
              name="title"
              onChange={handleSetTitle}
            />
          </div>
          <div>
            author:
          <input
              type="text"
              value={author}
              name="author"
              onChange={handleSetAuthor}
            />
          </div>
          <div>
            url:
          <input
              type="text"
              value={url}
              name="url"
              onChange={handleSetUrl}
            />
          </div>
          <button onClick={handleAddBlog}>create</button>
        </div>
      }
      <button onClick={handleToggleVisible}>{visible ? "cancel" : "create blog"}</button>
    </div>
  )
}

export default BlogForm;