import React from 'react'

const LoginForm = props => {
  const { username, setUsername, password, setPassword, handleLogin, handleVisible, visible } = props;

  return (
    <div>
      {visible &&
        <form onSubmit={handleLogin}>
          <div>
            username
            <input
              type="text"
              value={username}
              name="Username"
              onChange={setUsername}
            />
          </div>
          <div>
            password
            <input
              type="password"
              value={password}
              name="Password"
              onChange={setPassword}
            />
          </div>
          <button type="submit">login</button>
        </form>
      }
      <button onClick={handleVisible}>{visible ? "cancel" : "login"}</button>
    </div>
  )
}

export default LoginForm;