import React from 'react'
import Blog from './Blog'

const Blogs = ({ blogs, onLike }) => {

  const renderBlogs = () => blogs.sort((a, b) => a.likes < b.likes ? 1 : -1).map((blog, index) => <Blog key={index} blog={blog} onLike={onLike} />)

  return (
    <div>
      {renderBlogs()}
    </div>
  )
}

export default Blogs